import React, { useContext } from 'react';
import { BrowserRouter as Router, Route, Switch, Redirect } from 'react-router-dom';

import { ThemeContext } from './contexts/ThemeContext';
import { BlogPage, ProjectPage, Sony } from './pages'

import Main from "./pages/Main/Main";

import { BackToTop } from './components'
import ScrollToTop from './utils/ScrollToTop'

import './App.css'

function App() {
  var today = new Date();
  var date = today.getFullYear()+'-'+(today.getMonth()+1)+'-'+today.getDate();
  var time = today.getHours() + ":" + today.getMinutes() + ":" + today.getSeconds();
  var dateTime = date+' '+time;

  const { theme } = useContext(ThemeContext);

  console.log("%cDEVELOPER PORTFOLIO", `color:${theme.primary}; font-size:50px`);
  console.log("%chttps://atramUCSD.github.io/personal-website-3", `color:${theme.tertiary}; font-size:20px`);
  console.log(dateTime)
  console.log("medium.com troubleshooting + adding /test prior to subpages to hopefully fix folder structure")
  // console.log = console.warn = console.error = () => {};

  return (
    <div className="app">
      <Router>
        <ScrollToTop/>
        <Switch>
          <Route exact path= '' component={Main} />
          <Route exact path="/test/blog" component={BlogPage} />
          <Route exact path="/test/projects" component={ProjectPage} />
          <Route exact path="/test/Sony" component={Sony} />
          <Redirect to="/" /> 
        </Switch>
      </Router>
      <BackToTop />
    </div>
  );
}

export default App;
